<template>
  <div class="setRulesValidate">
    <div>
      <span class="label">{{setOptionsValue()}}</span>
      <a-button size="small" type="link" @click="formModelConfig.show=true"></a-button>
    </div>
    <a-modal
      :destroyOnClose="true"
      title=""
      :width="1000"
      cancelText=""
      okText=""
      @ok="handleSubmitOptions"
      v-model="formModelConfig.show"
    >
      <div class="model_table_header">
        <a-button size="small" @click="handleAddTableData"></a-button>
      </div>
      <a-table size="small" :pagination="false" :columns="columns" :data-source="options" bordered>
        <template slot="operate" slot-scope="value,row,index">
          <a-button size="small" @click="handleDelTableData(index)"></a-button>
        </template>
      </a-table>
    </a-modal>
  </div>
</template>
<script>
// import { clone } from '@/utils/utils.js'

export default {
  name: 'setRulesValidate',
  props: {
    value: {},
    defaultOption: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  watch: {
    value(newV) {
      this.options = newV
    }
  },
  data() {
    return {
      formModelConfig: {
        show: false
      },
      columns: [
        {
          title: '',
          dataIndex: 'index',
          key: 'index',
          customRender: (v, row, index) => {
            return {
              children: index + 1
            }
          }
        },
        {
          title: '',
          dataIndex: 'required',
          key: 'required',
          customRender: (v, row, index) => {
            const dom = (
              <a-radio-group
                value={row.required}
                onChange={$Event => {
                  const {
                    target: { value }
                  } = $Event
                  row.required = value
                }}
                name="required"
              >
                <a-radio value={true}></a-radio>
                <a-radio value={false}></a-radio>
              </a-radio-group>
            )
            return {
              children: index === 0 ? dom : ''
            }
          }
        },
        {
          title: '',
          dataIndex: 'trigger',
          key: 'trigger',
          width: 200,
          customRender: (v, row) => {
            const dom = (
              <a-radio-group
                value={row.trigger}
                onChange={$Event => {
                  const {
                    target: { value }
                  } = $Event
                  row.trigger = value
                }}
                name="required"
              >
                <a-radio value="blur">blur</a-radio>
                <a-radio value="change">change</a-radio>
              </a-radio-group>
            )
            return {
              children: dom
            }
          }
        },
        {
          title: '',
          dataIndex: 'max',
          key: 'max',
          customRender: (v, row) => {
            const dom = (
              <a-input-number
                value={row.max}
                onChange={v => {
                  row.max = v
                }}
              ></a-input-number>
            )
            return {
              children: dom
            }
          }
        },
        {
          title: '',
          dataIndex: 'min',
          key: 'min',
          customRender: (v, row) => {
            const dom = (
              <a-input-number
                value={row.min}
                onChange={v => {
                  row.min = v
                }}
              ></a-input-number>
            )
            return {
              children: dom
            }
          }
        },
        {
          title: '',
          dataIndex: 'message',
          key: 'message',
          customRender: (v, row) => {
            const dom = (
              <a-input
                value={row.message}
                onChange={$Event => {
                  const {
                    target: { value }
                  } = $Event
                  row.message = value
                }}
              ></a-input>
            )
            return {
              children: dom
            }
          }
        },
        // {
        //   title: '',
        //   dataIndex: 'type',
        //   key: 'type'
        // },
        {
          title: '',
          dataIndex: 'whitespace',
          key: 'whitespace',
          customRender: (v, row) => {
            const dom = (
              <a-radio-group
                value={row.whitespace}
                onChange={$Event => {
                  const {
                    target: { value }
                  } = $Event
                  row.whitespace = value
                }}
                name="whitespace"
              >
                <a-radio value={true}></a-radio>
                <a-radio value={false}></a-radio>
              </a-radio-group>
            )
            return {
              children: dom
            }
          }
        },
        {
          title: '',
          dataIndex: 'operate',
          key: 'operate',
          scopedSlots: { customRender: 'operate' }
        }
      ],
      options: []
    }
  },
  mounted() {
    this.options = this.value
  },
  methods: {
    //
    handleAddTableData() {
      this.options.push({
        required: true,
        trigger: 'blur',
        max: 9999,
        min: 0,
        message: '',
        // type: 'any',
        whitespace: false
      })
    },
    //
    handleDelTableData(k) {
      this.options.splice(k, 1)
    },
    //
    handleSubmitOptions() {
      this.formModelConfig.show = false
      this.$emit('handleSubmitOptions', this.options)
      this.$emit('input', this.options)
    },
    setOptionsValue() {
      return `${this.options.length}`
    }
  }
}
</script>
<style lang="scss" scoped>
@import '~@/assets/layout.scss';
.setRulesValidate {
  width: 100%;
  height: 100%;
  .label {
    width: 40px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}
.model_table_header {
  width: 100%;
  @include flex-row-e-c;
  height: 40px;
}
</style>